div.WBody {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /*flex-wrap: wrap;*/
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    height: calc( (100vh - 10px) * 5 / 6 );
    margin: 0 10px 0 10px;
}

div.WBody > div {
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    -ms-flex-align: start;
        align-items: flex-start;
    text-align: center;
}

div.WBody > div > div {
    -ms-flex: 0 0 50%;
        flex: 0 0 50%;
}

div.WBody > div > div:nth-child(3) {
    /*flex: 0 0 35%;*/
    font-size: 2em;
}

div.WBody > div > div:nth-child(2n) {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 1.2em;
}

div.WBody > div > div:nth-child(1) svg {
    width: 3.5em;
    height: 3.5em;
}

div.WBody > div > div:nth-child(2n) > div {
    text-align: left;
    -ms-flex: 0 0 51%;
        flex: 0 0 51%;
}

div.WBody > div > hr {
    width: 80%;
}


