html {
	font-size: 16px;
}

body {
	background-color: #000000;
	color: #FFFFFF;
}

div.App {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: stretch;
	    align-items: stretch;
	-ms-flex-line-pack: start;
	    align-content: flex-start
}

div.picture, div.weather {
	border: 1px solid white;
	margin: 5px;
	height: calc(100vh - 10px);
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

div.picture {
	-ms-flex: 1 1 auto;
	    flex: 1 1 auto;
}

div.weather {
	-ms-flex: 0 1 200px;
	    flex: 0 1 200px;
	font-size: .7em;
}


